<template>
  <div v-if="page === 0">
      <MainPageComp @page="page = $event"/>
  </div>
  <div v-else-if="page === 1">
      <InputComp @inputValues="handleChange" @page="page = $event"/>
  </div>
  <div v-else-if="page === 2">
      <MultipleInputComp @multipleInputValues="handleMultipleChange" @page="page = $event"/>
  </div>
  <div v-else-if="page === 3">
      <LoadingComp/>
  </div>
  <div v-else-if="page === 4">
      <ResultComp :index="index" :adress="adress" :parameters="params"/>
  </div>
</template>

<script>
import MainPageComp from './components/MainPageComp.vue'
import MultipleInputComp from './components/MultipleInputComp.vue'
import InputComp from './components/InputComp.vue'
import ResultComp from './components/ResultComp.vue'
import LoadingComp from './components/LoadingComp.vue'
import axios from 'axios'
export default {
  name: 'App',
  data(){
    return{
      adress: '',
      multipleAdress: [],
      radius: '',
      params: {},
      page: 0,
      error: false,
      index: 0,
    }
  },
  methods: {
    handleChange({adress, radius, params}){
      this.adress = adress
      this.radius = radius
      this.params = params
      this.page = 3
      var json = JSON.stringify({"amount": 1, "radius": this.radius, "addresses": [this.adress]})
      axios
      .post('//localhost:8080/api/debug/index', json, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then((response) => {
        console.log(response.data)
        //this.index = response.data.value
      })
      .catch((error) => {
        console.log(error)
      })
      .finally(() => {
        this.page = 4
      })
    },
    handleMultipleChange({adress1, adress2, adress3, radius, params}){
      if(adress1 != "") this.multipleAdress.push(adress1);
      if(adress2 != "") this.multipleAdress.push(adress2);
      if(adress3 != "") this.multipleAdress.push(adress3);
      this.radius = radius
      this.params = params
      var json = JSON.stringify({"amount": this.multipleAdress.length, "radius": this.radius, "addresses": this.multipleAdress})
      axios
      .post('//localhost:8080/api/debug/index', json, {
        headers: {
          'Content-Type': 'application/json'
        }
      })
      .then((response) => {
        console.log(response.data)
        //this.index = response.data.value
      })
      .catch((error) => {
        console.log(error)
      })
      .finally(() => {
        this.page = 4
      })
    },
},
  components: {
    MainPageComp,
    MultipleInputComp,
    InputComp,
    LoadingComp,
    ResultComp
  }
}
</script>

<style>
body {
  background-color: #f7f8fa;
  margin-inline: 68px;
  margin-top: 55px;
}
div, button, input{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif !important;
}
</style>
